<template lang="en">
    <div>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog ui-draggable" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ title }}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                            <section id="main-content" class="main-content">
                                <section class="main-content">
                                    <div class="container-fluid p-0">
                                        <div class="p-0">
                                        <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveHolidayBank">
                                            <template v-slot:tgslot-AvailableClientsLang="{data}" v-if="showContractDiv">
                                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0" >
                                                    <div class="col-lg-12 float-left" id="divContract" style="display: block;">
                                                        <input id="hdnSelectedContractsEdit" type="hidden" value="">
                                                        <div class="col-12 p-0 d-flex flex-wrap mb-2">
                                                            <div class="mover-panel-box" id="divAvailableColumnsAll">
                                                                <div class="row m-0">
                                                                    <div class="heading-border">
                                                                        <h5 class="h5"><span>Available Clients Lang</span></h5>
                                                                    </div>
                                                                    <div class="pt-2 d-inline-block w-100">
                                                                        <div class="col-12 p-0 mt-0">
                                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                                                                <input id="txtAvailableColumnsInSetup" maxlength="50" class="form-control" type="text" placeholder="Search" autocomplete="off">
                                                                            </div>
                                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                                                                <div class="search-btm-btn background-0 text-left form-check-inline">
                                                                                    <a title="" id="ancSearchAvailableColumnsInSetup" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a>
                                                                                    <a href="javascript:;" title="" id="ancClearAvailableColumnsInSetup" class="btn btn-danger px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-refresh"></i></a>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div class="w-100 mt-2" style="display:none;">
                                                                        <div class="input-group d-flex mt-1">
                                                                            <div class="custom-control custom-checkbox"><input type="checkbox" id="chk_62" class="custom-control-input" style="display: none;"><label class="custom-control-label universal-custom-control-label pt-1" for="chk_62"></label></div>Select All
                                                                        </div>
                                                                    </div>
                                                                    <div id="columns" class="w-100 table-responsive mt-1 listingcontrol" style="height: 360px; "><ul class="list-group"><li id="norecordAvailable" class="norecords list-group-item" style="display: block;"><div class="text-danger text-center no-record">No record(s) found</div></li></ul></div>
                                                                </div>
                                                            </div>
                                                            <div class="mover-controls ancArrowdata">
                                                                <button type="button" id="aMoveAllColumnArrow" data-toggle-tooltip="tooltip" title="" data-original-title="Move All Right"><i aria-hidden="true" class="fa fa-angle-double-down fa-2x d-md-none"></i><i aria-hidden="true" class="fa fa-angle-double-right fa-2x d-none d-md-block"></i></button>
                                                                <button type="button" id="aColumnArrow" data-toggle-tooltip="tooltip" title="" data-original-title="Move Right"><i aria-hidden="true" class="fa fa-angle-down fa-2x d-md-none"></i><i aria-hidden="true" class="fa fa-angle-right fa-2x d-none d-md-block"></i></button>
                                                                <button type="button" id="aColumnRemoveArrow" data-toggle-tooltip="tooltip" title="" data-original-title="Move Left"><i aria-hidden="true" class="fa fa-angle-up fa-2x d-md-none"></i><i aria-hidden="true" class="fa fa-angle-left fa-2x d-none d-md-block"></i></button>
                                                                <button type="button" id="aMoveAllColumnRemoveArrow" data-toggle-tooltip="tooltip" title="" data-original-title="Move All Left"><i aria-hidden="true" class="fa fa-angle-double-up fa-2x d-md-none"></i><i aria-hidden="true" class="fa fa-angle-double-left fa-2x d-none d-md-block"></i></button>
                                                            </div>
                                                            <div class="mover-panel-box" id="divAvailableColumnsAll">
                                                                <div class="row m-0">
                                                                    <div class="heading-border">
                                                                        <h5 class="h5"><span>Selected Clients Lang</span></h5>
                                                                    </div>
                                                                    <div class="pt-2 d-inline-block w-100">
                                                                        <div class="col-12 p-0 mt-0">
                                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                                                                <input id="txtSelectedColumnsInSetup" maxlength="50" class="form-control" type="text" placeholder="Search" autocomplete="off">
                                                                            </div>
                                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                                                                <div class="search-btm-btn background-0 text-left form-check-inline">
                                                                                    <a title="" id="ancSearchSelectedColumnsInSetup" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a>
                                                                                    <a href="javascript:;" title="" id="ancClearSelectedColumnsInSetup" class="btn btn-danger px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-refresh"></i></a>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div class="w-100 table-responsive mt-2 listingcontrol">
                                                                        <ul id="visiblecolumn" class="list-group" style="height: 360px; overflow-y: auto;"><li id="norecordVisibleColumn" class="norecords list-group-item ui-sortable-handle"><div class="text-danger text-center no-record">No record(s) found</div></li></ul>
                                                                    </div>
                                                                    <div class="w-100 float-left">
                                                                        <span id="spnselectedContracts" class="invalid-feedback" style="display:none;">Client is required</span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </template>
                                        <!-- {{CompanyHolidayListData}} --> 
                                            <template v-slot:tgslot-AvailableHolidayListing="{data}">
                                                <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Company" SubModuleCode="CompanyHoliday"
                                                IdentityColumn="Bank_Holiday_Id" :HeaderText="$t('CompanyHoliday')" :ListData="CompanyHolidayListData" :HeaderData="holidayColumnNames"
                                                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
                                                SearchPosition="top" :SearchFields="SearchHolidayFields" 
                                                :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                                                :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                                                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                                v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                                                    <template v-slot:slotdata="{ data }">
                                                        <template v-if="data.column.COLUMN_NAME == 'IsRecurring'">
                                                            <div class="form-group mb-0">
                                                                <label class="switch">
                                                                    <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                                        v-model="data.row.IsRecurring"
                                                                        v-on:change="checkForAutoCreateEnable(data.row,'IsRecurring')"
                                                                        v-disabled="disabled">
                                                                    <span class="slider round" style="cursor:default;">
                                                                        <span class="slider-yes">{{ $t('Yes') }}</span>
                                                                        <span class="slider-no">{{ $t('No') }}</span>
                                                                    </span>
                                                                </label>
                                                            </div>
                                                        </template>
                                                        <template v-if="data.column.COLUMN_NAME == 'Bank_Holiday_Type'"> 
                                                            <div class="custom-control custom-radio custom-control-inline">
                                                                <input type="radio" class="custom-control-input" v-on:change="checkForAutoCreateEnable(data.row,'IsFixedDate')" :id="'radiofixed'+data.row.Bank_Holiday_Id" :name="'radioholidaytype'+data.row.Bank_Holiday_Id" :value="1" v-model="data.row.IsFixedDate">
                                                                <label class="custom-control-label" :for="'radiofixed'+data.row.Bank_Holiday_Id" :class="{'disabled': data.row.Old_Holiday_id > 0 && data.row.Old_Holiday_id != null}">Fixed</label>
                                                            </div>
                                                            <div class="custom-control custom-radio custom-control-inline">
                                                                <input type="radio" class="custom-control-input" v-on:change="checkForAutoCreateEnable(data.row,'IsFixedDate')" :id="'radioObserved'+data.row.Bank_Holiday_Id" :name="'radioholidaytype'+data.row.Bank_Holiday_Id" :value="0" v-model="data.row.IsFixedDate">
                                                                <label class="custom-control-label" :for="'radioObserved'+data.row.Bank_Holiday_Id" :class="{'disabled': data.row.Old_Holiday_id > 0 && data.row.Old_Holiday_id != null}">Observed</label>
                                                            </div>
                                                        </template>
                                                    </template>
                                                </tg-list>
                                            </template>
                                        </dynamic-form>
                                        </div>
                                    </div>
                                </section>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    
    <style>
    .singleselectlook .multiselect__select::before {
        z-index: 99;
    }
    
    .singleselectlook .multiselect__tags {
        padding: 8px 30px 0 8px !important;
        min-height: 36px !important;
    }
    
    .singleselectlook .multiselect__input {
        margin-bottom: 0px;
    }
    </style>
    
    <script>
    import {
        defineComponent,
    } from '@vue/composition-api'
    import Schema from "@/views/Company/CompanyHoliday/CompanyHolidaySchema";
    import {
        extend,
        ValidationObserver,
        ValidationProvider
    } from 'vee-validate';
    import {
        ConvertCustomFieldIntoDynamicFormObj,
        ConvertDynamicFormObjToCustomFieldObj
    } from "@/assets/common/vue-common.js"
    import DataService from "@/services/DataService";
    import Toggle from '@vueform/toggle';
    import moment from 'moment'
    extend('required', {
        validate(value) {
            console.log("checkk")
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1
            };
    
        },
        message: 'The {_field_} is required'
        ///computesRequired: true
    });
    export default defineComponent({
       // name: "Manage Departemts",
        components: {
            ValidationObserver,
            ValidationProvider,
            Toggle
        },
        props: {
            CompanyHolidayData: {
                type: Array,
            },
            addApiUrl: {
                type: String
            },
            emitName: {
                type: String
            },
            moduleName: {
                type: String
            },
            subModule: {
                type: String
            },
            title: {
                type: String
            },
            recordId: {
                type: Number
            },
            addUserGuide:{
                type: String
            }
        },
        
        data() {
            return {
                startTime: null,
                endTime: null,
                hoursDifference: null,  
                fileData:[],  
                CompanyHolidayListData: [],
                SearchHolidayFields: Schema.topSearchHolidaySchema,
                holidayColumnNames: Schema.holidayColumnSchema,       
                editor: this.CLASSIC_EDITOR,
                department: {
                    name: '',
                    status: {
                        name: "active",
                        id: 1
                    },
                    visibaleToclients: false,
                    visibaleToTicting: false,
                    description: '',
                },
                buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                    onClick: this.SaveHolidayBank,
                },   
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                }],
                FormSchema: [
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "SelectField",
                                label: this.$t('HBLocation'),
                                name: "Select Location",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                    showAddIcon: false,
                                },
                                validationRules: "required",
                                disabled: false,
                            },{
                                astype: "CheckBoxField",
                                label: "",
                                name: "HolidayList",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [{name:"Company",value:"Company"},{name:"Client",value:"Client"}],
                                    showAddIcon: false,
                                    onChange: this.GetCheckBoxValue
                                },
                                validationRules: "",
                            },{
                                astype: "RadioField",
                                label: this.$t('SelectYear'),
                                name: "SelectYear",
                                value: "2024",
                                placeholder: "",
                                visibility: true,
                                config: {
                                    options: [{
                                        value: 2023,
                                        name: "Preview Year (2023)"
                                    },
                                    {
                                        value: 2024,
                                        name: "Current Year (2024)"
                                    },
                                    {
                                        value: 2025,
                                        name: "Next Year (2025)"
                                    }],
                                    showAddIcon: false,
                                    onChange: this.GetRadioValue
                                },
                                validationRules: "",
                            },
                        ]
                    },
                    {
                        layoutType:'single',
                        Data:[
                            {
                                astype: "SlotField",
                                label: "",
                                name: "AvailableClientsLang",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                    showAddIcon: false,
                                },
                                validationRules: "",
                            },
                        ]
                    },
                    {
                        layoutType:'single',
                        Data:[
                            {
                                astype: "SlotField",
                                label: "",
                                name: "AvailableHolidayListing",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                    showAddIcon: false,
                                },
                                validationRules: "",
                            },
                        ]
                    }
                ],
                isLoading: false,
                showContractDiv:false,
                GetSaveHolidayData:[]
            }
    
        },
        created:async function () {
            await this.FetchData();
            await this.GetLocationDDL();
        },
        methods: {
            checkForAutoCreateEnable(getRowData){
                var vm = this;
                var checkexist = vm.GetSaveHolidayData.find(x=> x.holidaybank_id === getRowData.Bank_Holiday_Id);
                if(checkexist != undefined){
                    checkexist.IsRecurring = getRowData.IsRecurring == false ? 0 : 1;
                    checkexist.IsFixedDate = getRowData.IsFixedDate;
                }else{
                    vm.GetSaveHolidayData.push({
                        client_id:'',
                        locations:vm.FormSchema[0].Data[0].value,
                        holidaybank_id:getRowData.Bank_Holiday_Id,
                        country_id:getRowData.Bank_Holiday_Country_Id,
                        IsRecurring:getRowData.IsRecurring == false ? 0 : 1,
                        IsFixedDate:getRowData.IsFixedDate
                    })
                }
            },
            async GetLocationDDL(){
                var vm = this;
                var url=`RequestFrom=${'COMPANY_PARENT_LOCATION'}`;
                await DataService.CompanyHolidayMultipleLocation(url).then(function (response) {
                    if (response.data.result != "" && response.data.result != null) {
                        if (response.data.result.length > 0) {
                            response.data.result.forEach(element => {                        
                                vm.FormSchema[0].Data[0].config.options.push({
                                    value:element.COMPANY_LOCATION_ID,
                                    name:element.LOCATION_NAME
                                })
                            });
                            vm.$refs.dynamicForm.UpdateKeyValue();
                        }                        
                    }
                });
            },
            async FetchData() {
                var vm = this;
                vm.isLoading = true;
                var url = `moduleName=${vm.moduleName}&subModuleCode=${vm.subModule}`;
                await DataService.HolidayBankListing(url).then(function(response) {
                    console.log(response);
                    if(response.data != '' && response.data != null) {
                        if(response.data.result.length > 0) {
                            console.log(response.data.result);
                            vm.holidayColumnNames.forEach(column=>{
                                if(column.COLUMN_NAME == "Bank_Holiday_Type" || column.COLUMN_NAME == "IsRecurring"){
                                    column.settings = {
                                        isInSlot: true,
                                    };
                                }
                            })
                            vm.CompanyHolidayListData = response.data.result;
                            // vm.holidayColumnNames = Object.keys(vm.CompanyHolidayListData[0]);
                            // console.log(vm.holidayColumnNames);
                        }
                    }
                })
            },
            async SaveHolidayBank() {
                //debugger;
                var vm = this;
                var params = `year=${vm.FormSchema[0].Data[2].value}`;
                var obj ={
                    MainData : vm.GetSaveHolidayData
                }
                DataService.AddHolidayInBank(params,obj)
                .then(function(response){
                    console.log(response);
                    var output = response.data.result;
                    if(output != null){
                            if(output.status == 'Success' && output.code == '1'){
                                this.ShowAlert(this.$t('SavedSuccessfully'), "success", false, this.$t("Alert"));
                            }

                            if(output.status == 'Failure' && output.code == '0'){
                                this.ShowAlert(this.$t('ErrorWhileSavingRecords'), "danger", false, this.$t("Alert"));
                            }                            
                    }

                });
            },

            closePopup() {
                this.$emit('close')
    
            },
            GetCheckBoxValue(){
                var vm = this;
                if(vm.FormSchema[0].Data[1].value == 'Client'){
                    vm.showContractDiv = true;
                }else{vm.showContractDiv = false;}
            },
    
        }
    
    });
    </script>
    